import axios from 'axios';

// [] Create React App process.env 
//  REACT_APP_ 
const baseURL = process.env.REACT_APP_API_BASE_URL || 'https://rqoufedpoguc.sealosgzg.site';

const api = axios.create({
    baseURL: `${baseURL}`,
    timeout: 15000,
});

api.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('token');
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

api.interceptors.response.use(
    (response) => {
        return response;
    },
    async (error) => {
        const originalRequest = error.config;
        
        //  401  token 
        if (error.response?.status === 401 && !originalRequest._retry && 
            !originalRequest.url.includes('/auth/refresh')) {
            originalRequest._retry = true;
            
            try {
                //  token
                const refreshResponse = await refreshToken();
                if (refreshResponse?.token) {
                    //  token
                    originalRequest.headers.Authorization = `Bearer ${refreshResponse.token}`;
                    // 
                    return api(originalRequest);
                }
            } catch (refreshError) {
                console.error(' token :', refreshError);
                //  token 
                localStorage.removeItem('token');
                // 
                if (!window.location.pathname.includes('/login')) {
                    window.location.href = '/login';
                }
                return Promise.reject(refreshError);
            }
        }
        
        // 
        console.error("API Error Interceptor:", error);
        let errorMsg = '';
        
        if (error.response) {
            const { status, data } = error.response;
            console.error(`API Error: Status ${status}`, data);
            
            if (status === 401) {
                localStorage.removeItem('token');
                errorMsg = '';
                if (!window.location.pathname.includes('/login')) {
                    window.location.href = '/login';
                }
            } else if (data?.detail) {
                errorMsg = Array.isArray(data.detail) 
                    ? data.detail.map(err => `${err.loc ? err.loc.join('.')+': ' : ''}${err.msg}`).join('; ')
                    : data.detail;
            } else {
                errorMsg = data?.error?.message || `: ${status}`;
            }
        } else if (error.request) {
            console.error('API Error: No response received', error.request);
            errorMsg = '';
        } else {
            console.error('API Error: Request setup error', error.message);
            errorMsg = `: ${error.message}`;
        }
        
        return Promise.reject({ message: errorMsg, originalError: error });
    }
);

// 

// 
const setAuthToken = (token) => {
  if (token) {
    api.defaults.headers.common['Authorization'] = `Bearer ${token}`;
  } else {
    delete api.defaults.headers.common['Authorization'];
  }
};
// 
const login = async (username, password) => {
    const loginUrl = `${baseURL}/api/v1/auth/token`;
    const params = new URLSearchParams();
    params.append('username', username);
    params.append('password', password);

    try {
        const response = await axios.post(loginUrl, params, {
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded',
            },
        });
        
        //  AuthContext 
        return {
            success: true,
            token: response.data.access_token,
            user: {
                username: username,
                role: response.data.role || 'user'
            },
            role: response.data.role || 'user'  //  role 
        };
    } catch (error) {
        console.error(':', error);
        const message = error.response?.data?.detail || '';
        return {
            success: false,
            message: message
        };
    }
};

// 
const register = async (username, password, email) => {
  try {
    const response = await api.post('api/v1/auth/register', {
      username,
      password,
      email
    });

    // 
    return {
      success: true,
      user: {
        id: response.data.id,
        username: response.data.username,
        role: response.data.role || 'user' // user
      }
    };
  } catch (error) {
    console.error(':', error);
    return {
      success: false,
      message: error.message || ''
    };
  }
};

