import React, { createContext, useState, useEffect, useCallback } from 'react';
import apiClient from '../services/apiClient';
import { toast } from '../components/common/Toast';

// 
export const AuthContext = createContext({
  isAuthenticated: false,
  user: null,
  token: null,
  loading: false,
  error: null,
  role: null,
  login: () => {},
  register: () => {},
  logout: () => {},
  clearError: () => {}
});

// 
export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [token, setToken] = useState(localStorage.getItem('token'));
  const [isAuthenticated, setIsAuthenticated] = useState(!!token);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [role, setRole] = useState(localStorage.getItem('userRole') || 'user');

  // 
  const clearError = () => setError(null);

  // 
  const clearAuth = useCallback(() => {
    //  localStorage 
    localStorage.removeItem('token');
    localStorage.removeItem('userRole');
    localStorage.removeItem('user');
    localStorage.removeItem('authData');

    // 
    setToken(null);
    setUser(null);
    setRole('user');
    setIsAuthenticated(false);
    setLoading(false);
    setError(null);

    //  API 
    apiClient.setAuthToken(null);
  }, []);

  // 
  const setAuth = useCallback((userData, authToken, userRole) => {
    if (!authToken) {
      clearAuth();
      return;
    }
    
    localStorage.setItem('token', authToken);
    localStorage.setItem('userRole', userRole || 'user');
    
    setToken(authToken);
    setUser(userData || { username: 'user' }); //  user  null
    setRole(userRole || 'user');
    setIsAuthenticated(true);
    setLoading(false);
    apiClient.setAuthToken(authToken);
  }, [clearAuth]);

  // 
  const login = async (username, password) => {
    setLoading(true);
    setError(null);
    
    try {
      const response = await apiClient.login(username, password);
      
      if (response && response.success) {
        // 
        const userData = response.user || { username, role: response.role || 'user' };
        const role = response.role || response.user?.role || 'user';
        
        setAuth(userData, response.token, role);
        toast.success('');
        return {
          success: true,
          user: userData,
          token: response.token,
          role: role
        };
      } else {
        const errorMsg = response?.message || '';
        setError(errorMsg);
        setLoading(false);
        toast.error(errorMsg);
        return { success: false, message: errorMsg };
      }
    } catch (err) {
      const errorMessage = err.message || err.response?.data?.message || '';
      console.error(':', err);
      setError(errorMessage);
      setLoading(false);
      toast.error(errorMessage);
      return { success: false, message: errorMessage };
    }
  };

  // 
  const register = async (username, password, email) => {
    setLoading(true);
    setError(null);
    
    try {
      const response = await apiClient.register(username, password, email);
      
      if (response.success) {
        setAuth(response.user, response.token, response.user?.role);
        toast.success('');
        return true;
      } else {
        setError(response.message || '');
        setLoading(false);
        toast.error(response.message || '');
        return false;
      }
    } catch (err) {
      const errorMessage = err.response?.data?.message || '';
      setError(errorMessage);
      setLoading(false);
      toast.error(errorMessage);
      return false;
    }
  };

  // 
  const logout = () => {
    clearAuth();
    toast.success('');
  };

  // token
  const checkAuth = useCallback(async () => {
    const currentToken = localStorage.getItem('token');
    if (!currentToken) {
      clearAuth();
      return;
    }
    
    try {
      // APItoken
      apiClient.setAuthToken(currentToken);
      
      // 
      const userData = await apiClient.getUserInfo();
      
      if (userData && userData.id) {  // 
        setUser(userData);
        setRole(userData.role || 'user');
        setIsAuthenticated(true);
        setToken(currentToken);
        setLoading(false);
      } else {
        // token
        try {
          const refreshResult = await apiClient.refreshToken();
          if (refreshResult?.success && refreshResult.token) {
            setAuth(refreshResult.user, refreshResult.token, refreshResult.role);
          } else {
            clearAuth();
          }
        } catch (refreshErr) {
          console.error('token:', refreshErr);
          clearAuth();
        }
      }
    } catch (err) {
      console.error(':', err);
      clearAuth();
    }
  }, [clearAuth, setAuth, setUser, setRole, setToken, setLoading]);

  // 
  useEffect(() => {
    checkAuth();
  }, [checkAuth]);

  // 
  const authContextValue = {
    isAuthenticated,
    user,
    token,
    loading,
    error,
    role,
    login,
    register,
    logout,
    clearError
  };

  return (
    <AuthContext.Provider value={authContextValue}>
      {children}
    </AuthContext.Provider>
  );
};

export default AuthProvider; 